import React from "react";
import PropTypes from 'prop-types';
import { graphql,Link }from 'gatsby';
import Img from 'gatsby-image';

import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Card from 'components/Card';
import Title from 'components/Title';

export const queryImage =  graphql`
query {
  project1: file(relativePath: { eq: "Gatsby-Course.jpg" }) {
    childImageSharp {
      fixed(width: 128, height: 128) {
        ...GatsbyImageSharpFixed
      }
    }
   }  
    project2: file(relativePath: { eq: "kassellabs.jpg" }) {
      childImageSharp {
        fixed(width: 128, height: 128) {
          ...GatsbyImageSharpFixed
        }
      }
    }
   project3:   file(relativePath: { eq: "maca.jpg" }) {
        childImageSharp {
          fixed(width: 128, height: 128) {
            ...GatsbyImageSharpFixed
          }
        }
      } 
  }
`;
const Projects = ({data }) =>(
<Layout>
    <SEO 

      pageTitle="Projects"

       description="Front end developer, developing apps and web pages"
     keywords="React, Node, Gatsby, front end, developer, API"
    />    
    <section className="section is-size-4-desktop is-size-5-touch">    
    <Title>Projects</Title>
    <p className="has-text-centered" >
      
      Some of my projects, see more on my
        {' '}
        <a href= "https://github.com/luanorlandi/" >
            GitHub
        </a>
        .
    </p>

      <div className="columns is-centered" >
      <div className="column is-half is-offset-one-quarter"> 
      <Card 
      title= "Gatsby Course"
      subtitle= "Build your personal website using React"
      link= "https://www.udemy.com/gatsby-crie-seu-site-pessoal/"
      tags={['Gatsby', 'React']}
      image= { <Img fixed={data.project1.childImageSharp.fixed} alt='Gatsby Course'/> }       
      />
       <Card 
      title= "kassel Labs"
      subtitle= "Helping people express their creativity and imagination"
      link= "https://kassellabs.io/"
      tags={['React']}
      image= { <Img fixed={data.project2.childImageSharp.fixed} alt=''/> }       
      />
       <Card 
      title= "Tic-tac-porg"
      subtitle= "Play tic-tac-porg with Star Wars theme"
      link= "https://luanorlandi.github.io/tic-tac-porg/"
      tags={['React','PWA']}
      image= { <Img fixed={data.project3.childImageSharp.fixed} alt=''/> }       
      /> 
       </div>
       </div> 
       <p className="has-text-centered" >
       Check out my
         {' '}
         <Link to="/blog" >blog</Link>
        .          
         </p>       
  </section>
      </Layout>
   );

Projects.propTypes ={
  data: PropTypes.object.isRequired,
};


export default Projects;